@media screen and (min-width: 320px) {
  html::before {
    content: "320px\A P: iPod Touch, iPhone 5";
    background-color: firebrick;
    display: block;
    white-space: pre;
    color: white;
    border-radius: 5px;
    border: 1px solid thistle;
    box-shadow: 1px 1px 5px 1px #333;
    font-size: 14px;
    font-weight: bold;
    padding: 2px 5px;
    position: fixed;
    right: 1%;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    top: 0em;
    opacity: 0.7;
  }
}

