/* w3c */
#n_johnny {
  margin-top: 4rem;
  width: 100%;
  /* The Modal (background) */
  /* Modal Content */
  /* The Close Button */
}

#n_johnny .n_philip {
  display: flex;
  flex-direction: column;
}

#n_johnny .n_sylvia > div:last-child,
#n_johnny .n_allen > div:last-child,
#n_johnny .n_marvin > div:last-child {
  border-color: transparent;
}

#n_johnny .n_marvin > div:first-child {
  border-top: 1px solid #d9d9d9;
}

#n_johnny .n_thelma,
#n_johnny .n_josephine,
#n_johnny .n_carrie {
  background-color: #ffffff;
  border-bottom: 1px solid #d9d9d9;
  padding: 1.5rem 0.5rem 1.5rem;
}

#n_johnny .modal {
  background-color: black;
  /* Fallback color */
  background-color: rgba(15, 98, 168, 0.4);
  /* Black w/ opacity */
  display: none;
  /* Hidden by default */
  height: 100%;
  /* Full height */
  left: 0;
  margin-left: 0;
  overflow: auto;
  /* Enable scroll if needed */
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  /* Stay in place */
  top: 0;
  width: 100%;
  /* Full width */
  z-index: 100;
  border: 10px solid red;
}

#n_johnny .modal.active {
  display: flex;
}

#n_johnny .modal-content {
  background-color: #fefefe;
  width: 80%;
  max-width: 60rem;
  position: relative;
}

#n_johnny .closemodal {
  float: right;
  font-weight: bold;
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  background-color: #808080;
  box-shadow: 1px 0px 2px #333;
}

#n_johnny .closemodal {
  position: absolute;
  right: -15px;
  top: -15px;
  width: 36px;
  height: 36px;
}

#n_johnny .closemodal:before, #n_johnny .closemodal:after {
  position: absolute;
  left: 16px;
  top: 8px;
  content: ' ';
  height: 20px;
  width: 5px;
  background-color: #fff;
}

#n_johnny .closemodal:before {
  transform: rotate(45deg);
}

#n_johnny .closemodal:after {
  transform: rotate(-45deg);
}

#n_johnny .closemodal:hover,
#n_johnny .closemodal:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

#n_johnny #desc {
  padding: 1em;
  flex: auto;
  width: 100%;
}

#n_johnny .modal-bio-wrapper {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

#n_johnny .modalwrapper form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#n_johnny .modal-header, #n_johnny .modal-footer {
  background-color: #666666;
  width: 100%;
  padding: 1rem;
}

#n_johnny .modal-header {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
}

#n_johnny .modal-body {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  padding: 1rem;
}

#n_johnny .modal-body label {
  padding: 1rem 0;
  text-align: center;
}

#n_johnny .modal-body input[type="file"] {
  padding: 1rem 0.7rem;
  font-size: 1.5rem;
  width: 100%;
  font-family: 'Open Sans', sans-serif;
  background: linear-gradient(359deg, #ededed 0%, white 30%, white 67%, #ededed 100%);
  border-radius: 0.4rem;
  transition: all 0.3s ease-out;
}

#n_johnny .modal-body input[type="file"]:hover {
  background: linear-gradient(359deg, #d6d6d6 0%, white 31%, white 63%, #d6d6d6 100%);
}

#n_johnny .modal-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1rem;
}

#n_johnny .modal-footer .cancel-upload.cancel-upload {
  background-color: #bcfbbb;
}

#n_johnny .modal-footer input, #n_johnny .modal-footer .cancel-upload {
  font-size: 1.5rem;
  padding: 0.5rem 2rem;
  border: transparent;
}

#file_notes {
  height: 30rem;
  overflow: hidden;
}

#file_notes.max-open {
  height: auto;
}

#notes_toggle {
  width: 100%;
  margin: 0.5rem 0 0;
  height: 3rem;
  border-radius: 0.4rem;
  border: 0;
  padding: 0.5rem;
  width: 100%;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.6rem;
}

#grad_cover {
  display: none;
  position: absolute;
  bottom: 3rem;
  height: 10rem;
  width: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.11) 2%, white 97%);
}

#grad_cover.showing {
  display: block;
}

